// 物品 Schema
const ItemSchema = z.object({
    名称: z.string(),
    描述: z.string(),
    品阶: z.string(),
    数量: z.coerce.number(),
});
// 宗门势力 Schema
const FactionSchema = z.object({
    所在地: z.string(),
    核心特点: z.string(),
    最高战力: z.string(),
    阵营: z.enum(['正道', '魔道', '中立']),
    规模: z.enum(['超大宗门', '大宗门', '小宗门', '微型宗门', '特殊势力']),
    备注: z.string().optional(),
});
// 功法 Schema
const TechniqueSchema = z.object({
    品阶: z.enum(['凡阶', '黄阶', '玄阶', '地阶', '天阶', '仙阶', '圣阶']),
    属性: z.string().describe('如:金系、木系、火系等'),
    效果描述: z.string(),
    修炼要求: z.string().optional(),
});
// 法宝兵器 Schema
const TreasureSchema = z.object({
    品阶: z.enum(['凡器', '法器', '灵器', '法宝', '灵宝', '仙器', '圣器', '道器']),
    类型: z.string().describe('如:剑、刀、枪、防御、飞行、储物等'),
    特性: z.string(),
    当前持有者: z.string().optional(),
});
// 地点 Schema
const LocationSchema = z.object({
    所属区域: z.enum(['天层', '神州', '东苍', '南炎', '西庚', '北冥', '下层', '四海']),
    类型: z.enum(['秘境', '城镇', '宗门', '禁地', '遗迹', '自然地形']),
    危险度: z.coerce.number().transform(v => _.clamp(v, 0, 100)),
    特点: z.string(),
    可获得资源: z.array(z.string()).prefault([]),
});
