// 获取灵根元素颜色
export function getRootColor(root) {
    for (const [elem, data] of Object.entries(CONFIG.ELEMENTS)) {
        if (root.includes(elem)) {
            return data.color;
        }
    }
    return '#aaaaaa'; // 默认灰色
}
// 获取境界等级颜色
export function getRealmColor(level) {
    const majorIdx = Math.floor((level - 1) / 4);
    const colors = [
        '#888888', // 练气 - 灰色
        '#44aa44', // 筑基 - 绿色
        '#4488ff', // 金丹 - 蓝色
        '#aa44ff', // 元婴 - 紫色
        '#ff4444', // 化神 - 红色
        '#ffaa00', // 炼虚 - 橙色
        '#ffdd44', // 合体 - 金色
        '#ffffff', // 大乘 - 白色
        '#ff88ff', // 渡劫 - 粉色
    ];
    return colors[majorIdx] || '#888888';
}
// 获取危险度颜色
export function getDangerColor(danger) {
    if (danger >= 90)
        return '#ff0000';
    if (danger >= 70)
        return '#ff4400';
    if (danger >= 50)
        return '#ff8800';
    if (danger >= 30)
        return '#ffcc00';
    return '#44aa44';
}
