// ============================================================================
// 动态世界书管理脚本 - 根据上下文智能启用/禁用世界书条目
//
// 功能：根据当前区域、境界、人物等上下文自动启用/禁用世界书条目，节省 token
// ============================================================================

import { Schema } from '../踏月寻仙-测试版/schema';

// ============================================================================
// 配置项
// ============================================================================

const CONFIG = {
    // 要管理的世界书名称（修改为你的世界书名）
    WORLDBOOK_NAME: 'current',  // 'current' 表示当前角色卡的主世界书

    // 是否启用调试日志
    DEBUG: true,

    // 上下文窗口（检查最近几条消息）
    CONTEXT_WINDOW: 5,
};

// ============================================================================
// 规则定义 - 根据你的角色卡自定义这些规则
// ============================================================================

interface WorldbookRule {
    /** 条目名称匹配模式 */
    entryPattern: string | RegExp;
    /** 启用条件 */
    shouldEnable: (context: Context) => boolean;
    /** 优先级（数字越大越优先，默认 0） */
    priority?: number;
}

interface Context {
    /** 当前所在区域 */
    currentRegion: string;
    /** 最近消息内容 */
    recentMessages: string[];
    /** MVU 变量数据 */
    mvuData: ReturnType<typeof Schema.parse> | null;
    /** 当前聊天 ID */
    chatId: string;
}

const RULES: WorldbookRule[] = [
    // ============================================================================
    // 系统设定 - 始终启用（蓝灯）
    // ============================================================================
    {
        entryPattern: /^\[系统\]/,
        shouldEnable: () => true,
        priority: 100,
    },
    {
        entryPattern: /^\[mvu_update\]/,
        shouldEnable: () => true,
        priority: 100,
    },
    {
        entryPattern: /^变量列表$/,
        shouldEnable: () => true,
        priority: 100,
    },
    {
        entryPattern: /^变量更新规则$/,
        shouldEnable: () => true,
        priority: 100,
    },
    {
        entryPattern: /^变量输出格式$/,
        shouldEnable: () => true,
        priority: 100,
    },

    // ============================================================================
    // 地图相关 - 只在对应区域启用
    // ============================================================================
    {
        entryPattern: /^\[地图\]\s*神州/,
        shouldEnable: ctx => ctx.currentRegion === '神州',
        priority: 10,
    },
    {
        entryPattern: /^\[地图\]\s*魔域/,
        shouldEnable: ctx => ctx.currentRegion === '魔域',
        priority: 10,
    },
    {
        entryPattern: /^\[地图\]\s*东海/,
        shouldEnable: ctx => ctx.currentRegion === '东海',
        priority: 10,
    },

    // ============================================================================
    // 人物相关 - 只在提到时或在红颜列表中时启用
    // ============================================================================
    {
        entryPattern: /^\[角色\]\s*掌门/,
        shouldEnable: ctx => {
            // 只有在万法宗且最近消息提到掌门时启用
            return (
                ctx.mvuData?.本尊?.行踪?.当前区域 === '万法宗' &&
                ctx.recentMessages.some(msg => msg.includes('掌门'))
            );
        },
        priority: 5,
    },
    {
        entryPattern: /^\[角色\]\s*孟十七/,
        shouldEnable: ctx => {
            // 如果孟十七在红颜列表中，或最近消息提到她
            return (
                ctx.mvuData?.红颜?.['孟十七'] !== undefined ||
                ctx.recentMessages.some(msg => msg.includes('孟十七'))
            );
        },
        priority: 8,
    },

    // ============================================================================
    // 境界相关 - 根据当前境界启用
    // ============================================================================
    {
        entryPattern: /^\[境界\]\s*练气/,
        shouldEnable: ctx => {
            const level = ctx.mvuData?.本尊?.等级 ?? 1;
            return level >= 1 && level <= 4;
        },
        priority: 7,
    },
    {
        entryPattern: /^\[境界\]\s*筑基/,
        shouldEnable: ctx => {
            const level = ctx.mvuData?.本尊?.等级 ?? 1;
            return level >= 5 && level <= 8;
        },
        priority: 7,
    },
    {
        entryPattern: /^\[境界\]\s*金丹/,
        shouldEnable: ctx => {
            const level = ctx.mvuData?.本尊?.等级 ?? 1;
            return level >= 9 && level <= 12;
        },
        priority: 7,
    },

    // ============================================================================
    // 事件相关 - 只在最近消息提到时启用
    // ============================================================================
    {
        entryPattern: /^\[事件\]/,
        shouldEnable: ctx => {
            // 提取事件名（假设格式为 [事件] 事件名）
            return ctx.recentMessages.join(' ').length > 0;
        },
        priority: 3,
    },
];

// ============================================================================
// 核心逻辑
// ============================================================================

let isProcessing = false;

async function getContext(): Promise<Context> {
    // 等待 MVU 初始化
    await waitGlobalInitialized('Mvu');

    // 获取最近的消息
    const messages = getChatMessages(-CONFIG.CONTEXT_WINDOW);
    const recentMessages = messages.map(m => m.message || '');

    // 获取 MVU 数据
    let mvuData: ReturnType<typeof Schema.parse> | null = null;
    try {
        const variables = getVariables({ type: 'message', message_id: -1 });
        const statData = _.get(variables, 'stat_data', null);
        if (statData) {
            mvuData = Schema.parse(statData);
        }
    } catch (e) {
        if (CONFIG.DEBUG) {
            console.warn('[动态世界书] 无法获取 MVU 数据', e);
        }
    }

    const currentRegion = mvuData?.本尊?.行踪?.当前区域 ?? '未知';
    const chatId = SillyTavern.getCurrentChatId();

    return {
        currentRegion,
        recentMessages,
        mvuData,
        chatId,
    };
}

async function updateWorldbookEntries() {
    if (isProcessing) return;
    isProcessing = true;

    try {
        const context = await getContext();

        // 获取世界书名称
        let worldbookName = CONFIG.WORLDBOOK_NAME;
        if (worldbookName === 'current') {
            const charWorldbooks = getCharWorldbookNames('current');
            worldbookName = charWorldbooks.primary || '';
            if (!worldbookName) {
                if (CONFIG.DEBUG) {
                    console.warn('[动态世界书] 当前角色卡没有绑定主世界书');
                }
                return;
            }
        }

        let enabledCount = 0;
        let disabledCount = 0;

        // 按优先级排序规则
        const sortedRules = [...RULES].sort((a, b) => (b.priority ?? 0) - (a.priority ?? 0));

        // 使用 updateWorldbookWith 更新世界书
        await updateWorldbookWith(worldbookName, worldbook => {
            for (const entry of worldbook) {
                if (!entry.enabled) continue;  // 跳过已禁用的条目

                const entryName = entry.name || '';
                let shouldBeConstant = false;

                // 查找匹配的规则
                for (const rule of sortedRules) {
                    const matches =
                        typeof rule.entryPattern === 'string'
                            ? entryName.includes(rule.entryPattern)
                            : rule.entryPattern.test(entryName);

                    if (matches) {
                        shouldBeConstant = rule.shouldEnable(context);
                        break; // 找到第一个匹配的规则就停止
                    }
                }

                // 更新条目的 strategy.type
                const wasConstant = entry.strategy.type === 'constant';

                if (shouldBeConstant && !wasConstant) {
                    entry.strategy.type = 'constant';
                    enabledCount++;
                    if (CONFIG.DEBUG) {
                        console.log(`[动态世界书] ✅ 启用（蓝灯）: ${entryName}`);
                    }
                } else if (!shouldBeConstant && wasConstant) {
                    entry.strategy.type = 'selective';
                    disabledCount++;
                    if (CONFIG.DEBUG) {
                        console.log(`[动态世界书] ❌ 改为绿灯: ${entryName}`);
                    }
                }
            }

            return worldbook;
        });

        if (CONFIG.DEBUG && (enabledCount > 0 || disabledCount > 0)) {
            console.info(
                `[动态世界书] 更新完成: 启用蓝灯 ${enabledCount} 个, 改为绿灯 ${disabledCount} 个`,
            );
        }
    } catch (e) {
        console.error('[动态世界书] 更新失败', e);
    } finally {
        isProcessing = false;
    }
}

// ============================================================================
// 初始化
// ============================================================================

$(() => {
    errorCatched(async () => {
        console.info('[动态世界书] 脚本已加载');

        // 等待 MVU 初始化
        await waitGlobalInitialized('Mvu');

        // 初始检查
        await updateWorldbookEntries();

        // 监听消息接收事件
        eventOn(tavern_events.MESSAGE_RECEIVED, async () => {
            await updateWorldbookEntries();
        });

        // 监听消息发送事件
        eventOn(tavern_events.MESSAGE_SENT, async () => {
            await updateWorldbookEntries();
        });

        // 监听 MVU 变量更新
        eventOn(Mvu.events.VARIABLE_UPDATE_ENDED, async () => {
            await updateWorldbookEntries();
        });

        console.info('[动态世界书] 监听器已设置，将自动管理世界书条目');
        toastr.success('动态世界书管理已启用，将自动优化 Token', '踏月寻仙');
    })();
});